<template>
<div>
    <el-container>
        <!--左-->
        <el-aside width="170px">
            <el-menu
                    default-active="2"
                    class="el-menu-vertical-demo"
                    background-color="#2f3e4e"
                    text-color="#fff"
                    active-text-color="#ffd04b"
                    style="height: 100vh"
                    :router="true"
            >
                <!--LOGO-->
                <div class="logo" style="text-align: left;">
                    <i class="iconfont iconlogo"></i>
                    <span class="text">鲜果汇</span>
                </div>
                <el-submenu v-for="item in routes" :index="item.name">
                    <template #title><i class="iconfont iconzichanqingdan"></i>
                        <span>{{item.meta.title}}</span>
                    </template>
                    <el-menu-item v-for="nav in item.children" :index="nav.name" :route="{name:nav.name}">{{nav.meta.title}}</el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>

        <!--右-->
        <el-container style="background: #eee;">
            <!--右上-->
            <el-header>
                <el-row class="box">
                    <el-col :span="6">
                        <h3>鲜果汇后台管理系统</h3>
                    </el-col>
                    <el-col :span="6">
                        当前时间:{{now}}
                    </el-col>
                    <el-col :span="6">
                        当前账号:{{user}}
                    </el-col>
                    <el-col :span="6">
                        <el-button type="text" @click="logout">退出登录</el-button>
                    </el-col>
                </el-row>
            </el-header>
            <!--右下-->
            <el-main><router-view></router-view></el-main>
        </el-container>
    </el-container>
</div>
</template>

<script>
    import formatDate from "../utils/formatDate";
    export default {

        data() {
            return {
                now:"",
                user:sessionStorage.getItem('user'),
                routes:[]
            }
        },
        methods: {
            logout(){
                sessionStorage.removeItem('user');
                sessionStorage.removeItem('role');
                this.$router.push("login");
                location.reload();
            }
        },
        created() {
            setInterval(()=>{
                let timestemp=Date.now();
                this.now=formatDate(timestemp/1000)
            },1000);
        },
        mounted() {
            if(sessionStorage.getItem("role")==="1"){
                this.routes=this.$store.state.obj1.children;
            }else {
                this.routes=this.$store.state.obj2.children;
            }
        }
    }
</script>

<style scoped>
    /*左LOGO*/
    .el-aside .logo {
        background-color: #2f3e4e;
        color: #fff;
        height: 60px;
        padding: 0 25px;
        line-height: 60px;
    }
    .container .el-aside .logo i {
        font-size: 36px;
        margin: 12px 0;
        vertical-align: middle;
    }
    .iconlogo{
        font-size: 30px;
    }
    .logo .text {
        display: inline-block;
        color: #fff;
        font-size: 22px;
        height: 100%;
        line-height: 60px;
        padding: 0 2px;
        vertical-align: top;
    }

    span{
        margin: 0 6px;
    }

    /*右头部*/
    .el-header{
        height: 60px;
        line-height: 60px;
        border-bottom: 1px solid #ccc;
    }

</style>